<template>
  <div>
    <!-- 导航栏 -->
    <van-nav-bar title="影院" left-text="返回" right-text="按钮" left-arrow @click-left="onClickLeft" @click-right="onClickRight">
      <template #right>
        <van-icon name="search" size="18" />
      </template>
      <template #left>
        <span>{{cityName}}</span>
        <van-icon name="arrow-down" />
      </template>
    </van-nav-bar>

    <!-- 影院 -->
    <div class="cinema-box">
      <van-list v-model="loading" :immediate-check="false" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <div class="cinema-item" v-for="item in cinemaArr" :key="item.name">
          <div class="item-left">
            <div class="cinema-name">{{ item.name }}</div>
            <div class="cinema-path">{{ item.address }}</div>
          </div>

          <div class="item-right">
            <div class="cinema-price">￥{{ Math.ceil(item.lowPrice / 100) }}元 <span>起</span></div>
            <div class="cinema-distance">距离未知</div>
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex';
export default {
  name: 'Cinemas',
  data() {
    return {
      cinemaArr: [],
      loading: false,
      finished: false,
    }
  },
  mounted() {
    this.getCinemaList()
  },
  methods: {
    async getCinemaList() {
      const { data: res } = await this.$api.GetMoviesCinemaList()
      // console.log(res.cinemas)
      this.cinemaArr = res.cinemas
    },
    onClickLeft() {
      this.$router.push({name:'City'})
    },
    onClickRight() {
      this.$router.push({name:'Search'})
    },
    onLoad() {
      this.onLoad = false
      // 数据全部加载完成

      this.finished = true
    },
  },
  computed: {
    ...mapState(['cityName'])
  },
}
</script>

<style lang="less" scoped>
.cinema-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 75px;
  // background-color: pink;
  padding: 15px;

  .item-left {
    width: 250px;
    .cinema-name {
      height: 23px;
      color: #191a1b;
      font-size: 15px;
    }
    .cinema-path {
      height: 18px;
      color: #797d82;
      font-size: 12px;
      margin-top: 5px;
    }
  }
  .item-right {
    width: 70px;
    color: #ff5f16;
    .cinema-price {
      height: 23px;
      font-size: 15px;
      span {
        font-size: 10px;
      }
    }
    .cinema-distance {
      font-size: 11px;
      height: 18px;
      color: #797d82;
      margin-top: 5px;
      text-align: center;
    }
  }
}
/deep/.van-icon-arrow-down {
  color: #191a1b;
}
.cinema-box {
  margin-bottom: 50px;
}
</style>
